.home__header {
  display: none;
}

.promo-card__wrapper {
    padding-bottom: 80px;

    .promo-card {
        z-index: 2;
        @include media("above:medium") {
            box-shadow: 0 0 66px 3px rgba(0, 0, 0, 0.15);
        }
    }

    .promo-card--left {

        @include media("above:medium") {
            width: 320px;
            position: relative;
            float: right;
            top: -50%;
            right: 20%;
        }
    }

    .promo-card--right {
        position: relative;
        margin-top: 10px;
        @include media("above:medium") {
            top: 60px;
            right: 10%;
            margin-top: 0;
        }
        padding-left: 0;
        padding-right: 0;

        @include media("above:medium") {
            width: 500px;
        }
    }
}

.ksp__wrapper {
    margin: 80px 0;
    text-align: center;
}

.ksp__banner {
    @include media("above:medium") {
        padding: 60px 0;
    }
}

.ksp__wrapper__anchor--more {
    @include anchor(skate);
    margin-top: 40px;
}
