////
/// Video.video-banner Base
/// @group components
////


.video-banner__wrapper {
    list-style-type: none;
    margin-left: 0;
    margin-top: 0;
}

.video-banner {
    position: relative;
}

.video-banner__text__wrapper {
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;

    .video-banner__text {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 60%;

        .video-banner__text__title,
        .video-banner__text__subtitle {
            @extend %alpha;
            @include font-family(meta);
            color: $color-lightgrey;
            font-size: $font-size-alpha;
            font-weight: $font-weight-bold;
            line-height: $line-height-perfect-fifth;
            margin-bottom: $measure-large;
            margin-top: (-$measure-medium);
            text-shadow: 0 0 $border-width-hairline $color-black;
        }

        .video-banner__text__title {
            @include media("above:large") {
                margin-top: 12%;
            }

            @include media("above:medium", "below:large") {
                margin-top: 5%;
            }

            @include media("below:medium") {
                margin-top: 5%;
                font-size: $font-size-beta;
            }

            @include media("below:small") {
                font-size: $font-size-delta;
            }

            @include media("below:min") {
                font-size: $font-size-default;
            }
        }

        .video-banner__text__subtitle {
            font-size: $font-size-beta;

            @include media("below:medium") {
                font-size: $font-size-gamma;
            }

            @include media("below:min") {
                font-size: $font-size-default;
            }
        }
    }
}
