.feature-events {
    margin: 30px 0;
    position:relative;

    @include media("above:medium") {
        margin-bottom: 100px;
    }
}

.feature-events__cards__wrapper {
    @include grid-column(4);
}

.feature-events__cards__content {
    position: relative;

    time {
        display: inline-block;
        margin-bottom: 20px;
        font-weight: 100;
    }
}

.feature-events__cards__link {
    a {
        @include anchor(skate);
    }
    margin-top: 20px;
    margin-left: $measure-large;
}
